import React, { useState, useEffect } from "react";
import { useRouteMatch, Link } from "react-router-dom";
import NewGalleries from './newgallerylist'

import GalleryPost  from "./gallerylistpost";
import ReactPaginate from "react-paginate";

import { storage, db } from "../../services/firebase";
import firebase from 'firebase/app';

import {
  Card,
    Container,
    Row,
    Col,
} from "react-bootstrap";


export default function Home() {
    const [tutorials, setTutorials] = useState([]);



  useEffect(() => {
    const unmount = db.collection("galleries")
    .orderBy('createdAt','desc')
    .onSnapshot((snapshot) => {
      const tutorials = [];
      snapshot.forEach((doc) => {
        tutorials.push(
          {
            id: doc.id,
              ...doc.data(),
          });
      });
      setTutorials(tutorials);
    });
    return unmount;
  }, []);



  const [pageNumber, setPageNumber] = useState(0);
  const usersPerPage = 12;
  const pagesVisited = pageNumber * usersPerPage;

  const pageCount = Math.ceil(tutorials.length / usersPerPage);

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };
  return (




                        <>

                        <Container  bg="danger"  className="mt-4" align="center" >
                              <Row>
                              <Col lg={12}>
                          <NewGalleries />
                              </Col >
                              </Row>
                              </Container>




                  <Container  bg="danger"  className="mt-4" >
                        <Row>

                    {tutorials
                     .slice(pagesVisited, pagesVisited + usersPerPage)
                      .map((tutorial) => (
                          <Col lg={4}>
                      <Link to={`/gallery/${tutorial.id}`}>

                                    <GalleryPost
                                      id={tutorial.id}
                                      albumName={tutorial.albumName}
                                      images={tutorial.images}
                                    />


                      </Link>
                        </Col>
                    ))}


                              <div className="mt-4">
                              <ReactPaginate
                              pageCount={pageCount}
                              onPageChange={changePage}
                              breakClassName={'page-item'}
                              breakLinkClassName={'page-link'}
                              containerClassName={'pagination'}
                              pageClassName={'page-item'}
                              pageLinkClassName={'page-link'}
                              previousClassName={'page-item'}
                              previousLinkClassName={'page-link'}
                              nextClassName={'page-item'}
                              nextLinkClassName={'page-link'}
                              activeClassName={'active'}
                              />
                             </div>
                            </Row>
                    </Container>


                    </>


  );
}
