import React, { useState } from "react";
import { Input, TextareaAutosize ,  Button, IconButton, LinearProgress } from "@mui/material";

import { storage, db } from "./../../firebase";
import firebase from 'firebase/compat/app';
import {
    Card,
     Container,
      Row,
      Col,

} from "react-bootstrap";


export default function NewAlbumForm() {
  const [albumName, setAlbumName] = useState("");
  const [description, setDescription] = useState("");
  const [file, setFile] = useState(null)

  const onFileChange = (e) => {
    setFile(e.target.files[0])
  }


  const onUpload = async () => {
    const storageRef = storage.ref("samples/")
    const fileRef = storageRef.child(file.name)
    await fileRef.put(file)
    db.collection("samples")
    .doc()
    .set({
      albumName: albumName,
      createdAt:  firebase.firestore.FieldValue.serverTimestamp(),
      description: description,
      images: firebase.firestore.FieldValue
          .arrayUnion({
                name: file.name,
                url: await fileRef.getDownloadURL()
                      })
        });

    setAlbumName("");
    setDescription("");
  };

  return (
    <>




                                                <Card className='mt-4 mb-4'  lg={12}>
                                                  <Container >
                                                  <Row  >
                                                    <Col lg={4}>
                                                        <Card.Title><h2 align='center' >{albumName}</h2> </Card.Title>
                                                      <Card.Body >
                                                      <TextareaAutosize
                                                        maxRows={2}
                                                        aria-label="minimum height"
                                                        minRows={10}
                                                        placeholder="Enter Album Name "
                                                        style={{ width: 450 }}
                                                        value={albumName}
                                                        onChange={(e) => setAlbumName(e.target.value)}

                                                        />
                                                        <TextareaAutosize
                                                          maxRows={4}
                                                          aria-label="minimum height"
                                                          minRows={10}
                                                          placeholder="Enter Description "
                                                          style={{ width: 450 }}
                                                          value={description}
                                                          onChange={(e) => setDescription(e.target.value)}

                                                          />
                                                            <input type="file" onChange={onFileChange}/>
                                                              <button onClick={onUpload}>Create album</button>
                                                      </Card.Body>




                                                        </Col>
                                                          </Row>
                                                        </Container>
                                                </Card>
    </>
  );
};
