import React, { useState, useEffect } from "react";
import { useRouteMatch, Link } from "react-router-dom";
import NewContactMessage from './newmessage'


import ReactPaginate from "react-paginate";

import { storage, db } from "../../services/firebase";
import firebase from 'firebase/compat/app';

import {
  Card,
    Container,
    Row,
    Col,
} from "react-bootstrap";


export default function Home() {
  const [albums, setAlbums] = useState([]);

  useEffect(() => {
    const unmount = db.collection("contacts")
     .orderBy('createdAt','desc')
     .onSnapshot((snapshot) => {
      const tempAlbums = [];

      snapshot.forEach((doc) => {

        tempAlbums.push({ ...doc.data(), id: doc.id });
      });
      setAlbums(tempAlbums);
    });
    return unmount;
  }, []);




  function handleDelete(id) {
   if(window.confirm("Delete the item?")){
    db.collection('contacts')

      .doc(id)
      .delete()
      .then(() => {
        console.log('Document successfully deleted!');
      })
      .catch((error) => {
        console.error('Error removing document: ', error);
      });
    }else {
      console.error('Error removing document: ');
    }
  }



  const [pageNumber, setPageNumber] = useState(0);
  const usersPerPage = 12;
  const pagesVisited = pageNumber * usersPerPage;

  const pageCount = Math.ceil(albums.length / usersPerPage);

  const changePage = ({ selected }) => {
    setPageNumber(selected);
  };
const options = { year: 'numeric', month: 'long', day: 'numeric' };
  return (




                        <>

                        <Container  bg="danger"  className="mt-4" align="center" >
                              <Row>
                              <Col lg={12}>
                          <NewContactMessage />
                              </Col >
                              </Row>
                              </Container>







                    <div className="list row">
                      <div className="col-md-6">

                          <table className="table table-stripe">
                            <thead>
                              <tr>
                                <th>Title</th>
                                <th>date</th>
                                <th>image</th>
                                <th>category</th>
                                <th>Action</th>
                              </tr>
                            </thead>
                            <tbody>
                              {albums
                                 .slice(pagesVisited, pagesVisited + usersPerPage)
                                 .map(album => (
                                   <tr id={album.id}>
                                  <td>  {album.name} </td>
                                  <td>  {new Date(album.createdAt.seconds * 1000).toLocaleDateString("en-US")} </td>
                                  <td>  {album.email} </td>
                                  <td>  {album.message}  </td>
                                  <td>
                                          <button
                                            onClick={() => handleDelete(album.id)}

                                            className="btn btn-danger"
                                          >
                                            Delete
                                          </button></td>
                                </tr>
                              ))}
                            </tbody>
                          </table>

                          <div className="mt-4">
                          <ReactPaginate
                          pageCount={pageCount}
                          onPageChange={changePage}
                          breakClassName={'page-item'}
                          breakLinkClassName={'page-link'}
                          containerClassName={'pagination'}
                          pageClassName={'page-item'}
                          pageLinkClassName={'page-link'}
                          previousClassName={'page-item'}
                          previousLinkClassName={'page-link'}
                          nextClassName={'page-item'}
                          nextLinkClassName={'page-link'}
                          activeClassName={'active'}
                          />
                         </div>
                                </div>
                                      </div>
                    </>


  );
}
