import React, { useEffect, useState } from "react";
import { useAuthState } from "react-firebase-hooks/auth";
import { useNavigate, Link } from "react-router-dom";
import "./Dashboard.css";
import { auth, db, logout } from "./firebase";
import { query, collection, getDocs, where } from "firebase/firestore";


import {
    Navbar,
    Nav,
    Container,
    NavDropdown,
    Form,
    FormControl,
} from "react-bootstrap";
import {
    IconButton,
} from "@mui/material";
import SearchIcon from '@mui/icons-material/Search';


import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';

import Album from "./album/albumlist";
import Eventhome from "./events/eventhome";
import Mediahome from "./media/mediahome";
import Projecthome from "./project/projecthome";
import Researcherhome from "./researcher/researcherhome";
import Videohome from "./video/videohome";


import Sample1 from "./test/testlist";
import Sample1Upload from "./test/upload";

function Dashboard() {
  const [user, loading, error] = useAuthState(auth);
  const [name, setName] = useState("");
  const navigate = useNavigate();

  const fetchUserName = async () => {
    try {
      const q = query(collection(db, "users"), where("uid", "==", user.uid));
      const doc = await getDocs(q);
      const data = doc.docs[0].data();

      setName(data.name);
    } catch (err) {
      console.error(err);
      alert("An error occured while fetching user data");
    }
  };

  useEffect(() => {
    if (loading) return;
    if (!user) return navigate("/login");

    fetchUserName();
  }, [user, loading]);

const [value, setValue] = React.useState('1');
const handleChange = (event: React.SyntheticEvent, newValue: string) => {
  setValue(newValue);
};


  return (


      <div>
          <Navbar bg="light" expand="md">
              <Container>



                  <div>{name}</div>

                  <button className="dashboard__btn" onClick={logout}>
                    Logout
                  </button>
              </Container>
          </Navbar>






          <TabContext value={value}>
          <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
          <TabList onChange={handleChange} aria-label="lab API tabs example" centered>
          <Tab label="Eventhome"       value="1" />
          <Tab label="Mediahome"       value="2" />
          <Tab label="Projecthome"     value="3" />
          <Tab label="Researcherhome"  value="4" />
          <Tab label="Videohome"       value="5" />
          <Tab label="Album"           value="6" />
          <Tab label="Sample1"           value="7" />

          </TabList>
          </Box>
          <TabPanel value="1">    <Eventhome />        </TabPanel>
          <TabPanel value="2">    <Mediahome />        </TabPanel>
          <TabPanel value="3">    <Projecthome />      </TabPanel>
          <TabPanel value="4">    <Researcherhome />   </TabPanel>
          <TabPanel value="5">    <Videohome />        </TabPanel>
          <TabPanel value="6">    <Album />            </TabPanel>
          <TabPanel value="7">     <Sample1Upload />   <Sample1 />            </TabPanel>


          </TabContext>



      </div>









  );
}

export default Dashboard;
