

import React, { useState } from "react";
import ProjectDataService from "../../services/ProjectService";
import DatePicker from "react-datepicker";


const Tutorial = (props) => {
  const initialTutorialState = {
    id: null,
    title: "",
    category: "",

    projectLeader: "",
    agency: "",

    coopagency1: "",
    coopagency2: "",
    coopagency3: "",
    coopagency4: "",
    coopagency5: "",

    site1: "",
    site2: "",

    type: "",
    rationale: "",
    methodology: "",

    createdAt: "" ,
    imageUrl: "",
    published: true,

  };
  const [currentTutorial, setCurrentTutorial] = useState(initialTutorialState);
  const [message, setMessage] = useState("");
 const [createdAt, setCreatedAt] = useState(new Date());

  const { tutorial } = props;
  if (currentTutorial.id !== tutorial.id) {
    setCurrentTutorial(tutorial);
    setMessage("");
  }

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setCurrentTutorial({ ...currentTutorial, [name]: value });
  };

  const updatePublished = (status) => {
    ProjectDataService.update(currentTutorial.id, { published: status })
      .then(() => {
        setCurrentTutorial({ ...currentTutorial, published: status });
        setMessage("The status was updated successfully!");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const updateTutorial = () => {
    const data = {
      title: currentTutorial.title,
      category: currentTutorial.category,

      projectLeader: currentTutorial.projectLeader,
      agency: currentTutorial.agency,

      coopagency1: currentTutorial.agency,
      coopagency2: currentTutorial.agency,
      coopagency3: currentTutorial.agency,
      coopagency4: currentTutorial.agency,
      coopagency5: currentTutorial.agency,

      site1: currentTutorial.site1,
      site2: currentTutorial.site2,


      type: currentTutorial.type,
      rationale: currentTutorial.rationale,
      methodology: currentTutorial.methodology,


      createdAt: currentTutorial.createdAt,
      imageUrl: currentTutorial.imageUrl,
      category: currentTutorial.category,
    };

    ProjectDataService.update(currentTutorial.id, data)
      .then(() => {
        setMessage("The tutorial was updated successfully!");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const deleteTutorial = () => {
         if(window.confirm("Delete the item?")){
    ProjectDataService.delete(currentTutorial.id)

      .then(() => {
        props.refreshList();
      })
      .catch((e) => {
        console.log(e);
      });
    }else {
      console.error('Error removing document: ');
    }
  };
const options = { year: 'numeric' , month: 'long', day: 'numeric', time:"ss.SSS"};
  return (
    <div>
      {currentTutorial ? (
        <div className="edit-form">
          <h4>Project</h4>
          <form>




      <img    variant="top"   width="150" height="150"   src={currentTutorial.imageUrl}  />

      <div className="form-group">
        <label htmlFor="category">Category</label>

                  <select  onChange={handleInputChange}  name="category" id="category"  value=  {currentTutorial.category}       style={{ width: 500 }} >

                  <option  value="">Select Category</option>
                  <option  value="CLSD project">CLSD Project</option>
                  <option  value="Research and Development ">Research and Development </option>
                  <option  value="In-house Research">In-house Research</option>
                  <option  value="Others"> Others</option>
                 </select>
      </div>

          <div className="form-group">
            <label htmlFor="title">Title</label>
              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="title"
              name="title"
              value={currentTutorial.title}
              onChange={handleInputChange}
              style={{ width: 500 }}
                >
                </textarea>
          </div>

          <div className="form-group">
            <label htmlFor="agency"> Agency </label>
              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="agency"
              name="agency"
              value={currentTutorial.agency}
              onChange={handleInputChange}
              style={{ width: 500 }}
                >
                </textarea>
          </div>
          <div className="form-group">
            <label htmlFor="site"> Site </label>
              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="stite1"
              name="site1"
              value={currentTutorial.site1}
              onChange={handleInputChange}
              style={{ width: 300 }}
                >
                </textarea>
          </div>
          <div className="form-group">
            <label htmlFor="site">  </label>
              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="site2"
              name="site2"
              value={currentTutorial.site2}
              onChange={handleInputChange}
              style={{ width: 300 }}
                >
                </textarea>
          </div>


          <div className="form-group">
            <label htmlFor="coopagency1"> Cooperating Agency </label>
              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="coopagency1"
              name="coopagency1"
              value={currentTutorial.coopagency1}
              onChange={handleInputChange}
              style={{ width: 300 }}
                >
                </textarea>
          </div>

          <div className="form-group">

              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="coopagency2"
              name="coopagency2"
              value={currentTutorial.coopagency2}
              onChange={handleInputChange}
              style={{ width: 300 }}
                >
                </textarea>
          </div>
          <div className="form-group">

              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="coopagency3"
              name="coopagency3"
              value={currentTutorial.coopagency3}
              onChange={handleInputChange}
              style={{ width: 300 }}
                >
                </textarea>
          </div>
          <div className="form-group">

              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="coopagency4"
              name="coopagency4"
              value={currentTutorial.coopagency4}
              onChange={handleInputChange}
              style={{ width: 300 }}
                >
                </textarea>
          </div>
          <div className="form-group">

              <textarea
              rows="2"
              cols="50"
              className="form-control"
              id="coopagency5"
              name="coopagency5"
              value={currentTutorial.coopagency5}
              onChange={handleInputChange}
              style={{ width: 300 }}
                >
                </textarea>
          </div>



          <div className="form-group">
            <label htmlFor="methodology">Methodology</label>
              <textarea
              rows="10"
              cols="50"
              className="form-control"
              id="methodology"
              name="methodology"
              value={currentTutorial.methodology}
              onChange={handleInputChange}
              style={{ width: 500 }}
                >
                </textarea>
          </div>

          <div className="form-group">
            <label htmlFor="rationale">Rationale</label>
              <textarea
              rows="10"
              cols="50"
              className="form-control"
              id="rationale"
              name="rationale"
              value={currentTutorial.rationale}
              onChange={handleInputChange}
              style={{ width: 500 }}
                >
                </textarea>
          </div>



<div className="form-group">
  <label htmlFor="createdAt">createAt</label>
  <input
    type="datetime-local"
    className="form-control"
    id="createdAt"
    value= {currentTutorial.createdAt}
    onChange={handleInputChange}
    name="createdAt"
    style={{ width: 200 }}
  />
</div>





            <div className="form-group">
              <label>
                <strong>Status:</strong>
              </label>
              {currentTutorial.published ? "Published" : "Pending"}
            </div>
          </form>



          {currentTutorial.published ? (
            <button
              className="badge badge-primary mr-2"
              onClick={() => updatePublished(false)}
            >
              UnPublish
            </button>
          ) : (
            <button
              className="badge badge-primary mr-2"
              onClick={() => updatePublished(true)}
            >
              Publish
            </button>
          )}

          <button className="badge badge-danger mr-2" onClick={deleteTutorial}>
            Delete
          </button>

          <button
            type="submit"
            className="badge badge-success"
            onClick={updateTutorial}
          >
            Update
          </button>
          <p>{message}</p>
        </div>
      ) : (
        <div>
          <br />
          <p>Please click on a Tutorial...</p>
        </div>
      )}
    </div>
  );
};

export default Tutorial;
