import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { collection, addDoc } from "firebase/firestore";
import { db } from "./firebase";
import DatePicker from "react-datepicker";

const Create = () => {
  const [camp, setCamp] = useState("");
  const [dayMatch, setDayMatch] = useState("");

 const [createdAt, setCreatedAt] = useState(new Date());

  const [user, setUser] = useState("");
  const [status, setStatus] = useState("");

  const navigate = useNavigate();

  const reservationColle = collection(db, "reservations");

  const store = async (e) => {
    e.preventDefault();
    await addDoc(reservationColle, {
      camp: camp,
      dayMatch: dayMatch,
      user: user,
      createdAt: new Date(),
      status: status
    });
    navigate("/sample3");
  };
const options = { year: 'numeric' , month: 'long', day: 'numeric', time:"ss.SSS"};
  return (
    <div className="container">
      <div className="row">
        <div className="col">
          <h3>Create reservation</h3>
          <form onSubmit={store}>
            <div className="mb-3">
              <label className="form-label">Campo</label>
              <input
                value={camp}
                onChange={(e) => setCamp(e.target.value)}
                type="text"
                className="form-control"
              />
            </div>
            <div className="mb-3">
              <label className="form-label">Dia</label>
              <input
                value={dayMatch}
                onChange={(e) => setDayMatch(e.target.value)}
                type="text"
                className="form-control"
              />
            </div>
            <div className="mb-3">
              <label className="form-label">Usuario</label>
              <input
                value={user}
                onChange={(e) => setUser(e.target.value)}
                type="text"
                className="form-control"
              />
            </div>

            <div className="form-group">
              <label htmlFor="createdAt">createAt</label>

                          <DatePicker
                          selected={createdAt}
                          onChange={date => setCreatedAt(date)}
                          />
            </div>

            <div className="mb-3">
              <label className="form-label">Estado</label>
              <input
                value={status}
                onChange={(e) => setStatus(e.target.value)}
                type="text"
                className="form-control"
              />
            </div>
            <button type="submit" className="btn btn-primary">
              Guardar reserva
            </button>
          </form>
        </div>
      </div>
    </div>
  );
};

export default Create;
