
import React, { useState } from "react";
import TutorialDataService from "../services/researcherservice";
import DatePicker from "react-datepicker";


const Tutorial = (props) => {
  const initialTutorialState = {
    id: null,
      firstName: "",
        middleName: "",
          lastName: "",
          suffix: "",


    designation: "",
    classification: "",
    department: "",


      project: "",


    timestamp:  "" ,
    imageUrl: "",
    published: false,
  };
  const [currentTutorial, setCurrentTutorial] = useState(initialTutorialState);
  const [message, setMessage] = useState("");

  const { tutorial } = props;
  if (currentTutorial.id !== tutorial.id) {
    setCurrentTutorial(tutorial);
    setMessage("");
  }

  const handleInputChange = (event) => {
    const { name, value } = event.target;
    setCurrentTutorial({ ...currentTutorial, [name]: value });
  };

  const updatePublished = (status) => {
    TutorialDataService.update(currentTutorial.id, { published: status })
      .then(() => {
        setCurrentTutorial({ ...currentTutorial, published: status });
        setMessage("The status was updated successfully!");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const updateTutorial = () => {
    const data = {
        firstName: currentTutorial.firstName,
        middleName: currentTutorial.middleName,
        lastName: currentTutorial.lastName,
        suffix: currentTutorial.suffix,



      designation: currentTutorial.designation,
      classification: currentTutorial.classification,
      department: currentTutorial.department,

      project: currentTutorial.project,


      timestamp: currentTutorial.timestamp,
      imageUrl: currentTutorial.imageUrl,
    };

    TutorialDataService.update(currentTutorial.id, data)
      .then(() => {
        setMessage("The tutorial was updated successfully!");
      })
      .catch((e) => {
        console.log(e);
      });
  };

  const deleteTutorial = () => {
    TutorialDataService.delete(currentTutorial.id)
      .then(() => {
        props.refreshList();
      })
      .catch((e) => {
        console.log(e);
      });
  };
const options = { year: 'numeric' , month: 'long', day: 'numeric', time:"ss.SSS"};
  return (
    <div>
      {currentTutorial ? (
        <div className="edit-form">
          <h4>Tutorial</h4>
          <form>


{/*    <div className="form-group">
      <label htmlFor="title">Title</label>
      <input
        type="text"
        className="form-control"
        id="title"
        name="title"
        value={currentTutorial.title}
        onChange={handleInputChange}
      />
    </div>*/}


      <img    variant="top"   width="150" height="150"   src={currentTutorial.imageUrl}  />






                          <div className="form-group">
                            <label htmlFor="firstName">First Name</label>
                              <textarea
                              rows="1"
                              cols="50"
                              className="form-control"
                              id="firstName"
                              name="firstName"
                              value={currentTutorial.firstName}
                              onChange={handleInputChange}
                                >
                                </textarea>
                          </div>

                          <div className="form-group">
                            <label htmlFor="middleName">Middle Initial</label>
                              <textarea
                              rows="1"
                              cols="50"
                              className="form-control"
                              id="middleName"
                              name="middleName"
                              value={currentTutorial.middleName}
                              onChange={handleInputChange}
                                >
                                </textarea>
                          </div>


                                    <div className="form-group">
                                      <label htmlFor="lastName">Last Name</label>
                                        <textarea
                                        rows="1"
                                        cols="50"
                                        className="form-control"
                                        id="lastName"
                                        name="lastName"
                                        value={currentTutorial.lastName}
                                        onChange={handleInputChange}
                                          >
                                          </textarea>
                                    </div>


                                                                        <div className="form-group">
                                                                          <label htmlFor="suffix">Suffix</label>
                                                                            <textarea
                                                                            rows="1"
                                                                            cols="50"
                                                                            className="form-control"
                                                                            id="suffix"
                                                                            name="suffix"
                                                                            value={currentTutorial.suffix}
                                                                            onChange={handleInputChange}
                                                                              >
                                                                              </textarea>
                                                                        </div>


          <div className="form-group">
            <label htmlFor="designation">Designation</label>
              <textarea
              rows="1"
              cols="50"
              className="form-control"
              id="designation"
              name="designation"
              value={currentTutorial.designation}
              onChange={handleInputChange}
                >
                </textarea>
          </div>




                   <div className="form-group">
                     <label htmlFor="classification">Classification</label>

                               <select  onChange={handleInputChange}  name="classification" id="classification"  value=  {currentTutorial.classification}  >
                          
                              <option  value="Program Leader">Program Leader</option>
                              <option  value="Project Leader">Project Leader</option>
                              <option  value="Faculty Researcher">Faculty Researcher</option>
                              <option  value="Researcher"> Researcher</option>
                              </select>
                   </div>







          <div className="form-group">
            <label htmlFor="department">Department</label>
              <textarea
              rows="1"
              cols="50"
              className="form-control"
              id="department"
              name="department"
              value={currentTutorial.department}
              onChange={handleInputChange}
                >
                </textarea>
          </div>



          <div className="form-group">
            <label htmlFor="project">Project</label>
              <textarea
              rows="1"
              cols="50"
              className="form-control"
              id="project"
              name="project"
              value={currentTutorial.project}
              onChange={handleInputChange}
                >
                </textarea>
          </div>


{/*}
            <div className="form-group">
              <label htmlFor="createdAt">createAt</label>
              <input
                type="datetime-local"
                className="form-control"
                id="createdAt"
                required
                value= {new Date(currentTutorial.createdAt.seconds*1000).toLocaleDateString("en-US",options)}
                onChange={handleInputChange}
                name="createdAt"
              />
            </div>
*/}

{/*}
            <div className="form-group">
              <label htmlFor="createdAt">createdAt</label>
              <input
                type="datetime-local"
                className="form-control"
                id="createdAt"
                required
                  value= {new Date(currentTutorial.createdAt.seconds*1000).toLocaleDateString("en-US",options)}
                onChange={handleInputChange}
                name="createdAt"
              />
            </div>

            <DatePicker
            value= {new Date(currentTutorial.createdAt.seconds*1000).toLocaleDateString("en-US",options)}
            onChange={date => handleInputChange(date)}
                />
*/}



                  <div className="form-group">
                    <label htmlFor="createdAt">createdAt</label>
                    <input
                      type="datetime"
                      className="form-control"
                      id="timestamp"
                      required
                        value= {new Date(currentTutorial.timestamp.seconds*1000).toLocaleDateString("en-US",options)}
                      onChange={handleInputChange}
                      name="timestamp"
                    />
                  </div>



            <div className="form-group">
              <label>
                <strong>Status:</strong>
              </label>
              {currentTutorial.published ? "Published" : "Pending"}
            </div>
          </form>



          {currentTutorial.published ? (
            <button
              className="badge badge-primary mr-2"
              onClick={() => updatePublished(false)}
            >
              UnPublish
            </button>
          ) : (
            <button
              className="badge badge-primary mr-2"
              onClick={() => updatePublished(true)}
            >
              Publish
            </button>
          )}

          <button className="badge badge-danger mr-2" onClick={deleteTutorial}>
            Delete
          </button>

          <button
            type="submit"
            className="badge badge-success"
            onClick={updateTutorial}
          >
            Update
          </button>
          <p>{message}</p>
        </div>
      ) : (
        <div>
          <br />
          <p>Please click on a Tutorial...</p>
        </div>
      )}
    </div>
  );
};

export default Tutorial;
