import React, { useState } from "react";
import { useNavigate } from "react-router";
import { useUserAuth } from "./UserAuthContext";

import {
  Button,
    Navbar,
    Nav,
    Container,
    NavDropdown,
    Form,
    FormControl,
} from "react-bootstrap";
import Box from '@mui/material/Box';
import Tab from '@mui/material/Tab';
import TabContext from '@mui/lab/TabContext';
import TabList from '@mui/lab/TabList';
import TabPanel from '@mui/lab/TabPanel';


import Videohome from "./video/videohome";
import Eventhome from "./events/eventhome";
import Mediahome from "./media/mediahome";
import Researcherhome from "./researcher/researcherhome";
import Projecthome from "./project/projecthome";
import Literaturehome from "./literature/literaturehome";
import Galleryhome from "./gallery/gallerylist";
import TestGalleryhome from "./gallery/eventgallerylist";
import ContactMessagehome from "./contactmessage/messagelist";
import RnDProjecthome from "./rndproject/projecthome";


const Home = () => {
  const { logOut, user } = useUserAuth();
  const navigate = useNavigate();
  const handleLogout = async () => {
    try {
      await logOut();
      navigate("/adminlogin");
    } catch (error) {
      console.log(error.message);
    }
  };
  const [value, setValue] = React.useState('1');
  const handleChange = (event: React.SyntheticEvent, newValue: string) => {
    setValue(newValue);
  };
  return (
    <>



      <Navbar bg="light" expand="md">
          <Container>





              <Button variant="primary" onClick={handleLogout}>
                Log out
              </Button>
          </Container>
      </Navbar>

      <div className="p-4 box mt-3 text-center">
        Hello Welcome <br />
      </div>

      <TabContext value={value}>
      <Box sx={{ borderBottom: 1, borderColor: 'divider' }}>
      <TabList onChange={handleChange} aria-label="lab API tabs example" centered>
      <Tab label="Videohome"       value="1" />
          <Tab label="Eventhome"       value="2" />
            <Tab label="Mediahome"       value="3" />
              <Tab label="Researcherhome"       value="4" />
                <Tab label="Projecthome"       value="5" />
                <Tab label="Literaturehome"       value="6" />
                  <Tab label="Galleryhome"       value="7" />
                        <Tab label="TestGalleryhome"       value="8" />
                          <Tab label="ContactMessagehome"       value="9" />
                          <Tab label="RnDProjecthome"       value="10" />


      </TabList>
      </Box>
      <TabPanel value="1">    <Videohome />        </TabPanel>
      <TabPanel value="2">    <Eventhome />        </TabPanel>
      <TabPanel value="3">    <Mediahome />        </TabPanel>
      <TabPanel value="4">    <Researcherhome />        </TabPanel>
      <TabPanel value="5">    <Projecthome />        </TabPanel>
      <TabPanel value="6">    <Literaturehome />        </TabPanel>
        <TabPanel value="7">    <Galleryhome />        </TabPanel>
          <TabPanel value="8">    <TestGalleryhome />        </TabPanel>
            <TabPanel value="9">    <ContactMessagehome />        </TabPanel>
              <TabPanel value="10">    <RnDProjecthome />        </TabPanel>


      </TabContext>



    </>
  );
};

export default Home;
