import React, { useState, useEffect } from "react";
import { Link   } from "react-router-dom";
import Tutorial from "./event";

import { storage, db } from "../../services/firebase";
import firebase from 'firebase/app';


export default function Home() {
    const [tutorials, setTutorials] = useState([]);
    const [currentTutorial, setCurrentTutorial] = useState(null);
    const [currentIndex, setCurrentIndex] = useState(-1);



useEffect(() => {
  const unmount = db.collection("events")
  .orderBy('createdAt','desc')
  .onSnapshot((snapshot) => {
    const tutorials = [];
    snapshot.forEach((doc) => {
      tutorials.push(
        {
          id: doc.id,
            ...doc.data(),
        });
    });
    setTutorials(tutorials);
  });
  return unmount;
}, []);



  const refreshList = () => {
    setCurrentTutorial(null);
    setCurrentIndex(-1);
  };

  const setActiveTutorial = (tutorial, index) => {
    setCurrentTutorial(tutorial);
    setCurrentIndex(index);
  };

  function handleDelete(id) {
   if(window.confirm("Delete the item?")){
    db.collection('events')

      .doc(id)
      .delete()
      .then(() => {
        console.log('Document successfully deleted!');
      })
      .catch((error) => {
        console.error('Error removing document: ', error);
      });
    }else {
      console.error('Error removing document: ');
    }
  }


const options = { month: 'long', day: 'numeric' , year: 'numeric'};

    return (
      <div className="list row">
        <div className="col-md-6">

            <table className="table table-stripe">
              <thead>
                <tr>
                  <th>Title</th>
                  <th>date</th>
                  <th>image</th>
                  <th>category</th>
                  <th>status</th>
                  <th>Action</th>
                </tr>
              </thead>
              <tbody>
                {tutorials.map(tutorial => (
                  <tr id={tutorial.id}>
                    <td> {tutorial.title} </td>

                    <td> {new Date(tutorial.createdAt).toLocaleDateString("en-US",options)} </td>
                    <td>  <img     variant="top"   width="50" height="50"   src={tutorial.imageUrl}  />  </td>
                    <td>
                      {tutorial.category}
                    </td>
                    <td>
                            {tutorial.published ? "Published" : "UnPublish"}
                    </td>
                    <td>
                    <button

                  onClick={() => setActiveTutorial(tutorial)}
                      className="btn btn-primary"
                  >
                    edit
                  </button>
                      <button
                        onClick={() => handleDelete(tutorial.id)}

                        className="btn btn-danger"
                      >
                        Delete
                      </button>


                    </td>
                  </tr>
                ))}
              </tbody>
            </table>

            </div>
            <div className="col-md-6">
              {currentTutorial ? (
                <Tutorial tutorial={currentTutorial} refreshList={refreshList} />
              ) : (
                <div>
                  <br />
                  <p>Please click on a Tutorial...</p>
                </div>
              )}
            </div>
          </div>



    );
  }
