import React, { useState } from "react";
import { Input, TextareaAutosize ,  Button, IconButton, LinearProgress } from "@mui/material";
import { PhotoCamera } from "@mui/icons-material";


import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";


import { storage, db } from "../../services/firebase";
import firebase from 'firebase/app';



export default function Upload({ username }) {



  const [title, setTitle] = useState("");

  const [published, setPublished] = useState(true);
  const [category, setCategory] = useState("");
 const [createdAt, setCreatedAt] = useState("");

 const [projectLeader, setProjectLeader] = useState("");
 const [agency, setAgency] = useState("");
 const [coopagency1, setCoopAgency1] = useState("");
 const [coopagency2, setCoopAgency2] = useState("");
 const [coopagency3, setCoopAgency3] = useState("");
 const [coopagency4, setCoopAgency4] = useState("");
 const [coopagency5, setCoopAgency5] = useState("");

 const [site1, setSite1] = useState("");
 const [site2, setSite2] = useState("");

 const [type, setType] = useState("");

 const [rationale, setRationale] = useState("")
 const [methodology, setMethodology] = useState("");


  const [imagePreview, setImagePreview] = useState('');
  const [image, setImage] = useState("");
  const [url, setUrl] = useState("");
  const [progress, setProgress] = useState(0);




  function handlePreview(image) {
    setImagePreview(URL.createObjectURL(image));
    setImage(image);
  }

  const handleChange = (e) => {
    if (e.target.files[0]) {
      handlePreview(e.target.files[0]);
    }
  };
  console.log('preview:', imagePreview);


  function handleUpload() {
    const uploadTask = storage.ref(`projectImages/${image.name}`).put(image);
    console.log("image:", image);
    console.log("image name:", image.name);
    uploadTask.on(
      "state_changed",
      (snapshot) => {
        //progress function
        const progress = Math.round(
          (snapshot.bytesTransferred / snapshot.totalBytes) * 100
        );
        setProgress(progress);
      },
      (error) => {
        //error function
        console.log(error);
        alert(error.message);
      },
      () => {
        //complete function
        storage
          .ref("projectImages")
          .child(image.name)
          .getDownloadURL()
          .then((url) => {
            //post image inside db
            console.log("url:" + url);
            db.collection("projects")
            .add({

              title: title,

              published: true,
              category: category,
              createdAt: createdAt,

              projectLeader: projectLeader,
              agency: agency,

              coopagency1: coopagency1,
              coopagency2: coopagency2,
              coopagency3: coopagency3,
              coopagency4: coopagency4,
              coopagency5: coopagency5,


              site1: site1,
              site2: site2,


              type: type,
              rationale: rationale,
              methodology: methodology,




              imageUrl: url,


            });
          });



          setTitle("");

          setPublished(true);
          setCategory("");
          setCreatedAt("");


          setProjectLeader("");
  setAgency("");



  setCoopAgency1("");
        setCoopAgency2("");
              setCoopAgency3("");
                    setCoopAgency4("");
                          setCoopAgency5("");

  setSite1("");
  setSite2("");

  setType("");
  setRationale("");
  setMethodology("");


        setProgress(0);
        setImage(null);
        setImagePreview('');
      }
    );
  }

  return (
    <>




          <h1>Upload Event </h1>



          <select onChange={(e) => setCategory(e.target.value)} name="category" id="category"     style={{ width: 950 }} required>
          <option  value="">Select Category</option>
          <option  value="CLSD project">CLSD Project</option>
          <option  value="Research and Development ">Research and Development </option>
          <option  value="In-house Research">In-house Research</option>
          <option  value="Others"> Others</option>
         </select>

         <br></br>

        <TextareaAutosize
          maxRows={4}
          aria-label="minimum height"
          minRows={10}
          placeholder="enter project title "
          value={title}
          onChange={(e) => setTitle(e.target.value)}
          className="input"
          style={{ width: 950 }}
          required
          />
          <br></br>



             <br></br>
          <TextareaAutosize
          maxRows={2}
          aria-label="Enter project leader "
          minRows={10}
          placeholder="Enter project leader "

          value={projectLeader}
          onChange={(e) => setProjectLeader(e.target.value)}
          className="input"
              style={{ width: 950 }}
              required
          />
             <br></br>

             <TextareaAutosize
               maxRows={2}
               aria-label="Enter implementing agency"
               minRows={10}
               placeholder="Enter implementing agency "
               value={agency}
               onChange={(e) => setAgency(e.target.value)}
               className="input"
               style={{ width: 950 }}
               required
               />


     <br></br>
     <TextareaAutosize
       maxRows={2}
       aria-label="Enter Cooperating agency"
       minRows={10}
       placeholder="Enter Cooperating agency "
       value={coopagency1}
       onChange={(e) => setCoopAgency1(e.target.value)}
       className="input"
       style={{ width: 950 }}
       required
       />

       <br></br>
       <TextareaAutosize
         maxRows={2}
         aria-label="Enter Cooperating agency"
         minRows={10}
         placeholder="Enter Cooperating agency "
         value={coopagency2}
         onChange={(e) => setCoopAgency2(e.target.value)}
         className="input"
         style={{ width: 950 }}
         required
         />

         <br></br>
         <TextareaAutosize
           maxRows={2}
           aria-label="Enter Cooperating agency"
           minRows={10}
           placeholder="Enter Cooperating agency "
           value={coopagency3}
           onChange={(e) => setCoopAgency3(e.target.value)}
           className="input"
           style={{ width: 950 }}
           required
           />

           <br></br>
           <TextareaAutosize
             maxRows={2}
             aria-label="Enter Cooperating agency"
             minRows={10}
             placeholder="Enter Cooperating agency "
             value={coopagency4}
             onChange={(e) => setCoopAgency4(e.target.value)}
             className="input"
             style={{ width: 950 }}
             required
             />
             <br></br>
             <TextareaAutosize
               maxRows={2}
               aria-label="Enter Cooperating agency"
               minRows={10}
               placeholder="Enter Cooperating agency "
               value={coopagency5}
               onChange={(e) => setCoopAgency5(e.target.value)}
               className="input"
               style={{ width: 950 }}
               required
               />

<br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter site "
          minRows={10}
          placeholder="Enter site"
          value={site1}
          onChange={(e) => setSite1(e.target.value)}
          className="input"
          style={{ width: 950 }}
          required
          />

          <br></br>

                  <TextareaAutosize
                    maxRows={2}
                    aria-label="Enter site "
                    minRows={10}
                    placeholder="Enter site"
                    value={site2}
                    onChange={(e) => setSite2(e.target.value)}
                    className="input"
                    style={{ width: 950 }}
                    required
                    />

     <br></br>
     <TextareaAutosize
       maxRows={2}
       aria-label="Enter rationale "
       minRows={10}
       placeholder="Enter rationale"
       value={rationale}
       onChange={(e) => setRationale(e.target.value)}
       className="input"
       style={{ width: 950 }}
       required
       />


     <br></br>

        <TextareaAutosize
          maxRows={2}
          aria-label="Enter methodoloogy "
          minRows={10}
          placeholder="Enter methodoloogy"
          value={methodology}
          onChange={(e) => setMethodology(e.target.value)}
          className="input"
          style={{ width: 950 }}
          required
          />



<div className="form-group">
  <label htmlFor="createdAt">createAt</label>
  <input
    type="datetime-local"
    className="form-control"
    id="createdAt"
    value= {createdAt}
    onChange={(e) => setCreatedAt(e.target.value)}
    name="createdAt"  style={{ width: 200 }}
      required
  />
</div>



        <input
          type="file"
          onChange={handleChange}
            className="input"
          style={{ display: "none" }}
          id="raised-button-file"
        />
        <label htmlFor="raised-button-file">
          <IconButton
            variant="raised"
            component="span"
            className="button"
            color="primary"
          >
          <img
        src={imagePreview||"https://thealmanian.com/wp-content/uploads/2019/01/product_image_thumbnail_placeholder.png"}
        alt=''
            className="image"
        width="150"
        height="150"
      />
            <PhotoCamera />
          </IconButton>
        </label>
        <LinearProgress
          variant="determinate"
          color="primary"
          value={progress}
          className="progress"
        />
        <Button variant="outlined" onClick={handleUpload}>
          Upload
        </Button>




    </>
  );
}
